import React, {Component} from 'react';
import './Header.css';
import * as PubSub from 'pubsub-js'

var Scroll = require('react-scroll');
var Link = Scroll.Link;

class Header extends Component {

    constructor(props) {
        super(props);
        console.log(PubSub);

        this.state = {
            showCurtain: 0,
            display: 'none',
            short: false,
            opacity: 0.7,
            shorturl: '',
            opacityResult: 0,
            opacityWaiting: 0,
            fat:'data:image/png;base64,',
            thin: 'data:image/png;base64,'
        };

    }



    componentWillMount() {
        const self = this;

        PubSub.subscribe('showCurtain', function(topic, product) {
            self.setState({
                showCurtain: 0.7,
                display: 'block',
                opacityResult: 0
            })
        })

        PubSub.subscribe('hideCurtain', function(topic, product) {
            self.setState({
                showCurtain: 0,
                display: 'block'
            })
        })

        PubSub.subscribe('hideResult', function(topic, product) {
            self.setState({
                opacityResult: 0
            })
        })

        PubSub.subscribe('showResult', function(topic, product) {
            self.setState({
                opacityResult: 1
            })
        })


        PubSub.subscribe('triggerError', function(topic, product) {
            self.setState({
                shorturl: 'make sure the url format is correct'

            })
        })


        PubSub.subscribe('showThin', function(topic, product) {
            self.setState({
                short: true,
                opacity: 1

            })
        })

        PubSub.subscribe('showFat', function(topic, product) {
            self.setState({
                short: false,
                opacity: 0.4
            })
        })

        PubSub.subscribe('setResult', function(topic, product) {
            console.log(product.url);
            self.setState({
                shorturl: 'http://' + product.url + '.urlsh.me'
            })
        })

        PubSub.subscribe('startLoading', function(topic, product) {
            self.setState({
                opacityWaiting: 1
            })
        })

        PubSub.subscribe('endLoading', function(topic, product) {
            self.setState({
                opacityWaiting: 0
            })
        })



    }



    render() {
        return (
            <div>
              <div className="cs-full">
                <div className="spinner" style={{opacity: this.state.opacityWaiting} }></div>

              <div className="cs-curtain" style={{opacity: this.state.showCurtain }}></div>

              <h1 className="cs-logo">URLSH!<span>subdomain-like and popular short urls</span></h1>
             
              <img style={{opacity:this.state.opacity, transition:'1s all'}} className="cs-char" src={this.state.short ? this.state.thin : this.state.fat }/>

                {this.props.children}
                <p style={{opacity:this.state.opacityResult, transition:'1s all'}}  className="cs-shortlink">&nbsp;<a target="_blank" href={this.state.shorturl}>{this.state.shorturl}</a></p>


                <p className="cs-see-more">
                <Link activeClass="active" to="results" spy={false} smooth={true} offset={0} duration={1000} >
                  see popular links
                </Link>
              </p>

            </div>
          <div className="decoration5"></div>

            <div className="decoration4"></div>
            <div className="decoration6"></div>


            </div>
        )
    }
}


export default Header;


